/* Teams Page - Matching TeamWithProposal style */
.teams-page {
    min-height: 100vh;
    background-color: var(--background);
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
    color: var(--text);
  }
  
  .teams-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
    gap: 1.5rem;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 2rem;
  }
  
  .team-card {
    background: var(--card-bg);
    border-radius: var(--radius-md);
    padding: 1.5rem;
    box-shadow: var(--shadow-sm);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
  }
  
  .team-card:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-md);
  }
  
  .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--border);
  }
  
  .team-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--border);
  }
  
  .team-header h2 {
    font-size: 1.25rem;
    margin: 0;
    color: var(--primary);
  }
  
  .team-meta h2 {
    font-size: 1.25rem;
    margin: 0;
    color: var(--primary);
  }
  
  .team-size {
    background-color: var(--primary-light);
    color: white;
    padding: 0.25rem 0.75rem;
    border-radius: 9999px;
    font-size: 0.75rem;
    font-weight: 500;
  }
  
  .toggle-proposals-btn {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    background: none;
    border: none;
    color: var(--primary);
    font-weight: 500;
    cursor: pointer;
    padding: 0.5rem;
    font-size: 0.875rem;
  }
  
  .chevron {
    transition: transform 0.2s ease;
    font-size: 0.75rem;
  }
  
  .chevron.expanded {
    transform: rotate(180deg);
  }
  
  .team-content {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
  
  .team-section {
    margin-bottom: 1rem;
  }
  
  .section-title {
    font-size: 0.9375rem;
    font-weight: 600;
    margin-bottom: 0.75rem;
    color: var(--text);
  }
  
  .tags-container {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
  }
  
  .research-tag {
    background-color: var(--primary-light);
    color: white;
    padding: 0.25rem 0.75rem;
    border-radius: 9999px;
    font-size: 0.75rem;
    font-weight: 500;
  }
  
  .members-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 0.75rem;
  }
  
  .member-card {
    background-color: var(--background);
    border-radius: var(--radius-sm);
    padding: 0.75rem;
  }
  
  .member-name {
    font-weight: 500;
    font-size: 0.875rem;
    margin-bottom: 0.25rem;
  }
  
  .member-fields {
    display: flex;
    flex-wrap: wrap;
    gap: 0.25rem;
  }
  
  .field-tag {
    background-color: var(--border);
    color: var(--text);
    padding: 0.15rem 0.5rem;
    border-radius: 9999px;
    font-size: 0.6875rem;
    font-weight: 500;
  }
  
  .proposals-container {
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
  }
  
  .no-proposals {
    color: var(--text-light);
    font-style: italic;
    text-align: center;
    padding: 1rem;
    background-color: var(--background);
    border-radius: var(--radius-sm);
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .teams-container {
      grid-template-columns: 1fr;
    }
    
    .members-grid {
      grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    }
  }
  
  /* Reuse RoomPage header styles */
  .header-container {
    background: var(--card-bg);
    padding: 2rem;
    box-shadow: var(--shadow-sm);
    margin-bottom: 2rem;
  }
  
  .header-container h1 {
    font-size: 1.5rem;
    font-weight: 600;
    margin-bottom: 0.25rem;
  }
  
  .back-button {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    padding: 0.75rem 1.5rem;
    background-color: white;
    color: var(--primary);
    border: 1px solid var(--primary);
    border-radius: var(--radius-sm);
    font-weight: 600;
    cursor: pointer;
    transition: all 0.2s ease;
  }
  
  .back-button:hover {
    background-color: var(--primary);
    color: white;
  }
  
  /* Loading and empty states */
  .loading {
    text-align: center;
    padding: 2rem;
    color: var(--text-light);
  }
  
  .empty-state {
    text-align: center;
    padding: 3rem;
    max-width: 500px;
    margin: 0 auto;
  }
  
  .empty-icon {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  .empty-state p {
    color: var(--text-light);
    margin-bottom: 1.5rem;
  }
  
  /* Enhanced Member Tags */
  .member-fields {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem; /* This ensures consistent spacing between tags */
    margin-top: 0.5rem;
  }
  
  .field-tag {
    background-color: var(--border);
    color: var(--text);
    padding: 0.35rem 0.75rem; /* Increased padding */
    border-radius: var(--radius-sm);
    font-size: 0.8125rem; /* Slightly larger font */
    font-weight: 500;
    line-height: 1.4;
    transition: all 0.2s ease;
    display: inline-flex;
    align-items: center;
  }
  
  .field-tag:hover {
    background-color: #e0e0e0; /* Slightly darker on hover */
    transform: translateY(-1px);
  }
  
  /* Member Card Enhancements */
  .member-card {
    background-color: var(--background);
    border-radius: var(--radius-sm);
    padding: 1rem; /* More padding */
    transition: transform 0.2s ease;
  }
  
  .member-card:hover {
    transform: translateY(-2px);
    box-shadow: var(--shadow-sm);
  }
  
  .member-name {
    font-weight: 600; /* Bolder name */
    font-size: 0.9375rem; /* Slightly larger */
    margin-bottom: 0.5rem; /* More space below name */
    color: var(--primary-dark);
  }
  
  /* Adjust Members Grid */
  .members-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(160px, 1fr)); /* Wider cards */
    gap: 1rem; /* More space between cards */
  }
  
  /* Responsive Adjustments */
  @media (max-width: 768px) {
    .members-grid {
      grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    }
    
    .field-tag {
      padding: 0.3rem 0.65rem;
      font-size: 0.7875rem;
    }
  }
  
  /* Enhanced Member Tags with Right/Left Margins */
  .member-fields {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem; /* Modern browsers - primary spacing method */
    margin: 0.5rem -0.25rem; /* Negative margin to compensate for tag margins */
  }
  
  .field-tag {
    background-color: var(--border);
    color: var(--text);
    padding: 0.35rem 0.75rem;
    border-radius: var(--radius-sm);
    font-size: 0.8125rem;
    font-weight: 500;
    line-height: 1.4;
    margin: 0.25rem; /* Equal margin on all sides */
    display: inline-flex;
    align-items: center;
    transition: all 0.2s ease;
  }
  
  /* Fallback for browsers without gap support */
  @supports not (gap: 0.5rem) {
    .member-fields {
      margin: 0.5rem -0.5rem; /* Larger negative margin */
    }
    .field-tag {
      margin: 0.5rem; /* Larger margins for fallback */
    }
  }
  
  /* Hover effects */
  .field-tag:hover {
    background-color: #e0e0e0;
    transform: translateY(-1px);
  }
  
  .team-meta h2 {
    font-size: 1.25rem;
    margin: 0;
    color: var(--primary-dark); /* Changed from var(--primary) */
  }
  
  .team-header h2 {
    font-size: 1.25rem;
    margin: 0;
    color: var(--primary-dark); /* Changed from var(--primary) */
  }

  .room-id-copy {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .copy-button {
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 4px;
    transition: opacity 0.2s ease-in-out;
    display: flex;
    align-items: center;
  }
  
  .copy-button:hover {
    opacity: 0.8;
  }
  
  .copy-button svg {
    color: #333;
  }
  